@import url('https://fonts.googleapis.com/css2?family=Kdam+Thmor+Pro&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300&display=swap');

*, *::before, *::after{
  margin:0;
  padding:0;
  box-sizing:border-box;
  cursor:pointer;
}
body{
  background:#004eb4
}
.container{
  display:flex;
  justify-content:center;
  align-items:center;
  flex-direction: column;
  width:60%;
  background:rgb(7,7,39);
  position:absolute;
  top:50%;
  left:50%;
  transform:translate(-50%,-50%) scale(1.3);
  color:white;
  height:300px;
  border-radius:5px;
  box-shadow: 0 0 50px rgba(0,0,0,0.2),
  inset 0 0 20px rgba(255,255,255,0.2)
}
.left-side{
  right:192px;
}
.right-side{
  left:192px;
  top:103px;
}
.side{
  display:flex;
  justify-content:center;
  align-items:center;
  background:rgb(18,18,58);
  width:50%;
  position:relative;
  height:100%;
  border-radius:0 0 7px 7px;
  text-align:center;
}
.title{
  position:absolute;
  top:0%;
  font-family:'Kdam Thmor Pro',sans-serif;
  transform:translateY(30px);
  font-size:2rem;
}
.bar-icon{
  left:50%;
}
.icon{
  position:absolute;
  transform:scale(3.2);
  z-index:400;
  top:75%;
}
.folder-icon{
  left:5%;
}
.emphasis{
  font-family:'Montserrat',sans-serif;
  font-size:18px;
}
.info{
  font-size:30px;
  font-family:'Kdam Thmor Pro',sans-serif;
  transition:0.3s;
}
.subscribeCount{
  font-size:30px;
  font-family: 'Kdam Thmor Pro',sans-serif;
  text-align:center;
}
.info:hover{
  background:rgba(255,255,255,0.1);
  box-shadow: 0 8px 32px 0 rgba(0,0,0,0.37);
  backdrop-filter:blur(4px);
  border-radius:2px;
  border:0.5px solid rgba(255,255,255,0.18);
  padding:5px;
}
.subscribeCount{
  transform:translateY(90px)
}
#profile{
  width:140px;
  z-index:400;
  position:absolute;
  top:30%;
  left:15%;
  border:2px solid #004eb4;
  border-radius:5px;
}
#profile:hover{
  transition:0.3s;
  transform:scale(1.03)
}